#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg {
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}
.introContent{
    height: 100vh;
    width: 100wh;
    padding: 2rem;
    font-size:3rem ;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.hello {
    font-size: 1.75rem;
    font-weight: 100vh;
}
.introName{
    color: yellow;

}
.introPara{
    font-size: medium;
    font-weight: 200;
    letter-spacing: 1px;

}
.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.5rem 2rem;
    border: none;
    border-radius: 2rem;
    align-items: center;
    justify-content: center;
    display: flex;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height:0.75rem ;

}

@media screen and (max-width:840px){
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 10vw;
    }
}